import { createCustomElement } from "@servicenow/ui-core";
import snabbdom from "@servicenow/ui-renderer-snabbdom";
import view from "./view";
import styles from "./styles.scss";
import { SPECIAL_INSTRUCTIONS, DELIVERY_ADDRESS } from "./constants";
/**
 * Provide the additional details (delivery address, special instructions) of given Catalog item.
 *
 * ```jsx
 * <sn-catalog-additional-details
 *		deliveryAddress={...}
 *		specialInstructions={...}
 *		hideDeliveryAddress={...}
 *		hideSpecialInstructions={...}
 *		/>
 * ```
 *
 * @seismicElement sn-catalog-additional-details
 * @summary Provide the additional details (delivery address, special instructions) of given Catalog item
 *
 */
createCustomElement("sn-catalog-additional-details", {
	renderer: { type: snabbdom },
	view,
	initialState: {
		showHeader: {
			deliveryAddress: true,
			specialInstructions: true
		}
	},
	properties: {
		/**
		 * Delivery address, where the catalog item should be delivered. Should be as follows
		 * {
		 *   label:"Delivery Address",
		 *   value:"1",
		 *   visible:true
		 * }
		 *
		 * @type {object}
		 */
		deliveryAddress: {
			default: {
				label: DELIVERY_ADDRESS,
				value: "",
				visible: true
			}
		},
		/**
		 * Special instructions for the catalog item. Should be as follows
		 * {
		 *   label:"Special instructions",
		 *   value:"1",
		 *   visible:true
		 * }
		 *
		 * @type {object}
		 */
		specialInstructions: {
			default: {
				label: SPECIAL_INSTRUCTIONS,
				value: "",
				visible: true
			}
		},
		/**
		 * Hide/Show the delivery address
		 *
		 * @type {Boolean}
		 */
		hideDeliveryAddress: {
			default: false
		},
		/**
		 * Hide/Show the special instructions
		 *
		 * @type {Boolean}
		 */
		hideSpecialInstructions: {
			default: false
		}
	},
	styles,
	dispatches: {
		/**
		 * Dispatched when delivery address updated
		 * @type {{value: string}}
		 */
		DELIVERY_ADDRESS_UPDATED: {},
		/**
		 * Dispatched when special instructions updated
		 * @type {{value: string}}
		 */
		SPECIAL_INSTRUCTIONS_UPDATED: {}
	}
});
