import "@servicenow/now-heading";
import "@servicenow/now-text-link";
import "@devsnc/sn-record-input-connected";
import "@devsnc/sn-glide-form-controls";
import {
	controlElementFactory as glideControlElementFactory,
	FieldType
} from "@devsnc/sn-glide-form-controls";
import {
	DELIVERY_ADDRESS_UPDATED,
	SPECIAL_INSTRUCTIONS_UPDATED
} from "./actions";
import {
	DETAILS_HEADER,
	DELIVERY_ADDRESS_LINK,
	SPECIAL_INSTRUCTIONS_LINK,
	DELIVERY_ADDRESS,
	SPECIAL_INSTRUCTIONS
} from "./constants";

const getControl = (field, dispatch, onChange, componentId) => {
	let {
		// eslint-disable-next-line no-unused-vars
		Control,
		props,
		children
	} = glideControlElementFactory.getFormControl(
		componentId,
		field.type,
		field,
		onChange,
		null,
		{}
	);
	return <Control {...props}>{children}</Control>;
};

const view = (
	{
		properties: {
			deliveryAddress,
			specialInstructions,
			hideDeliveryAddress,
			hideSpecialInstructions
		},
		showHeader,
		componentId
	},
	{ dispatch, updateProperties, updateState }
) => {
	if (
		!(hideDeliveryAddress && hideSpecialInstructions) &&
		deliveryAddress &&
		specialInstructions
	) {
		deliveryAddress.label = deliveryAddress.label || DELIVERY_ADDRESS;
		deliveryAddress.type = FieldType.TEXTAREA;
		specialInstructions.label =
			specialInstructions.label || SPECIAL_INSTRUCTIONS;
		specialInstructions.type = FieldType.TEXTAREA;

		const onDeliveryAddressChange = event => {
			dispatch(DELIVERY_ADDRESS_UPDATED, {
				value: event.value
			});
			updateProperties({
				deliveryAddress: { ...deliveryAddress, value: event.value }
			});
		};

		const onSpecialInstructionChange = event => {
			dispatch(SPECIAL_INSTRUCTIONS_UPDATED, {
				value: event.value
			});
			updateProperties({
				specialInstructions: {
					...specialInstructions,
					value: event.value
				}
			});
		};

		return (
			<div className="sc-additional-details">
				<now-heading
					label={DETAILS_HEADER}
					level="2"
					variant="header-secondary"
				/>
				{hideDeliveryAddress ? (
					""
				) : (
					<div className="sc-delivery-address">
						{showHeader.deliveryAddress ? (
							<now-text-link
								label={DELIVERY_ADDRESS_LINK}
								href="javascript:void(0)"
								download=""
								on-click={() =>
									updateState({
										showHeader: { ...showHeader, deliveryAddress: false }
									})
								}
								variant="primary"
								configAria={{}}
							/>
						) : (
							getControl(
								deliveryAddress,
								dispatch,
								onDeliveryAddressChange,
								`${componentId}-delivery-address`
							)
						)}
					</div>
				)}
				{hideSpecialInstructions ? (
					""
				) : (
					<div className="sc-special-instructions">
						{showHeader.specialInstructions ? (
							<now-text-link
								label={SPECIAL_INSTRUCTIONS_LINK}
								href="javascript:void(0)"
								download=""
								on-click={() =>
									updateState({
										showHeader: { ...showHeader, specialInstructions: false }
									})
								}
								variant="primary"
								configAria={{}}
							/>
						) : (
							getControl(
								specialInstructions,
								dispatch,
								onSpecialInstructionChange,
								`${componentId}-special-instructions`
							)
						)}
					</div>
				)}
			</div>
		);
	}
};

export default view;
